*, *::before, *::after{
    box-sizing: border-box;
}

body{
    background: linear-gradient(to right, #9078AD, #6C4E90, #321456);
}

.App{
    height: 100vh;
    width: 100%;
}

.calculator-grid{
    display: grid;
    justify-content: center;
    align-content: center;
    min-height: 80vh;
    grid-template-columns: repeat(4, 80px);
    grid-template-rows: minmax(80px, auto) repeat(5, 80px);
}

.calculator-grid > button{
    border-radius: 1rem;
    cursor:pointer;
    font-size: 1rem;
    font-weight: bold;
    color: black;
    border: 1px solid white;
    outline: none;
    background-color: #fafafa50;
    margin: 3px;
}
.calculator-grid > button:hover{
    background-color: #fafafa99;
}
.span-two{
    grid-column: span 2;
}
.output{
    border-radius: 1rem;
    grid-column: 1/-1;
    margin-bottom: .5rem;
    background-color: #9078AD;
    display: flex;
    padding: 10px;
    align-items: flex-end;
    justify-content: space-around;
    flex-direction: column;
    word-wrap: break-word;
    word-break: break-all;
}
.output .previous-operand{
    color: #6C4E90;
    font-size: 1.5rem;
}
.output .current-operand{
    color:  #1D053A;
    font-size: 2.5rem;
}

.title {
    color: #1D053A;
    font-family: roboto;
    text-align: center;
}